b5b;\r\n$color-table-bg: $color-white;\r\n$color-table-bg-1: #f5f5f5;\r\n$color-table-bg-2: #d7e4ff;\r\n$color-table-bg-3: #9ab7ea;\r\n$color-table-bg-warning: #fff8ac;\r\n//边框、线条定义\r\n$border-default: 1px solid $color-border;\r\n//边距尺寸定义\r\n$margin-default: 12px;\r\n$margin-s: 8px;\r\n@mixin mobile() {\r\n  @media screen and (max-width: 767px) {\r\n    @content;\r\n  }\r\n}\r\n\r\n@mixin mobile-xs() {\r\n  @media screen and (max-width: 359px) {\r\n    @content;\r\n  }\r\n}\r\n\r\n@mixin flex($flow: row nowrap, $jus: space-between, $ali: center) {\r\n  display: flex;\r\n  flex-flow: $flow;\r\n  justify-content: $jus;\r\n  align-items: $ali;\r\n}\r\n\r\n@mixin text-overflow {\r\n  text-overflow: ellipsis;\r\n  overflow: hidden;\r\n  white-space: nowrap;\r\n}\r\n\r\n@mixin clearfix-after {\r\n  &:after {\r\n    display: block;\r\n    content: \"\";\r\n    clear: both;\r\n  }\r\n}\r\n\r\n@mixin content {\r\n  .content {\r\n    @content;\r\n  }\r\n}\r\n\r\n@mixin btn-size($width: auto, $height: auto, $font-size: 12px) {\r\n  width: $width;\r\n  height: $height;\r\n  font-size: $font-size;\r\n  border-radius: $height;\r\n}\r\n\r\n@mixin btn($width: auto, $height: auto, $font-size: 12px) {\r\n  @include flex($jus: center);\r\n  @include btn-size($width, $height, $font-size);\r\n  line-height: 1;\r\n  background: $color-active;\r\n  color: $color-white;\r\n  border: none;\r\n  font-size: $font-size;\r\n  cursor: pointer;\r\n  &:hover,\r\n  &:active {\r\n    background: $color-active-light;\r\n  }\r\n  &.base-line-btn {\r\n    background: $color-white;\r\n    border: 1px solid $color-active;\r\n    color: $color-active;\r\n    &:hover,\r\n    &:active {\r\n      background: $color-white;\r\n      border-color: $color-active-light;\r\n      color: $color-active-light;\r\n    }\r\n  }\r\n}\r\n\r\n@mixin upload-btn {\r\n  position: relative;\r\n  overflow: hidden;\r\n  input[type=\"file\"] {\r\n    position: absolute;\r\n    left: -100%;\r\n    top: -100%;\r\n    width: 200%;\r\n    height: 200%;\r\n    opacity: 0;\r\n    cursor: pointer;\r\n  }\r\n}\r\n\r\n@mixin auto-scroll {\r\n  overflow: auto;\r\n  &::-webkit-scrollbar {\r\n    position: absolute;\r\n    top: 10px;\r\n    padding-top: 20px;\r\n    width: 6px;\r\n    height: 6px;\r\n  }\r\n  &::-webkit-scrollbar-thumb {\r\n    /*滚动条里面小方块*/\r\n    border-radius: 10px;\r\n    background-color: $color-input-border;\r\n  }\r\n  &::-webkit-scrollbar-track {\r\n    /*滚动条里面轨道*/\r\n    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);\r\n    /*border-radius: 10px;*/\r\n    // background: #EDEDED;\r\n  }\r\n}\r\n\r\n@function get-rem-size($px) {\r\n  @return $px/100 + \"rem\";\r\n}\r\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\r\n.summary-data-wrap {\r\n  .control-bar {\r\n    @include flex();\r\n    height: 72px;\r\n    padding: 0 $margin-default;\r\n    .time-filter-box {\r\n      select,\r\n      option {\r\n        height: 22px;\r\n      }\r\n      .year-select,\r\n      .month-select {\r\n        margin-right: 8px;\r\n        width: 90px;\r\n      }\r\n      .month-select {\r\n        margin-right: 0;\r\n      }\r\n      .tips {\r\n        margin-left: 16px;\r\n        padding-left: 24px;\r\n        line-height: 18px;\r\n        color: $color-warning-text;\r\n        position: relative;\r\n        &:before {\r\n          content: \"\";\r\n          position: absolute;\r\n          left: 0;\r\n          top: 0;\r\n          width: 18px;\r\n          height: 18px;\r\n          background: url(../assets/images/icons.png) 0 -40px no-repeat;\r\n        }\r\n      }\r\n    }\r\n    .btn-box {\r\n      .query-btn {\r\n        @include btn-size(80px, 25px);\r\n      }\r\n      .report-btn {\r\n        @include btn-size(130px, 25px);\r\n        margin-left: $margin-default;\r\n      }\r\n    }\r\n  }\r\n  .build-dialog {\r\n    .form-item {\r\n      @